/* ============
 * Routes File
 * ============
 *
 * The routes and redirects are defined in this file
 */


/**
 * The routes
 *
 * @type {object} The routes
 */
export default [
  // Home
  {
    path: '/home',
    name: 'home.index',
    component: require('pages/home/index/index.vue'),

    // If the user needs to be authenticated to view this page
    meta: {
      auth: false,
    },
  },

    // About
  {
    path: '/about',
    name: 'about.index',
    component: require('pages/about/index/index.vue'),

    // If the user needs to be authenticated to view this page
    meta: {
      auth: false,
    },
  },

      // Photo Archive
  {
    path: '/photo-archive',
    name: 'photo-archive.index',
    component: require('pages/photo-archive/index/index.vue'),

    // If the user needs to be authenticated to view this page
    meta: {
      auth: false,
    },
  },

      // Contact Me
  {
    path: '/contact',
    name: 'contact.index',
    component: require('pages/contact/index/index.vue'),

    // If the user needs to be authenticated to view this page
    meta: {
      auth: false,
    },
  },

  // Account
  {
    path: '/account',
    name: 'account.index',
    component: require('pages/account/index/index.vue'),

    // If the user needs to be authenticated to view this page
    meta: {
      auth: true,
    },
  },

  // Login
  {
    path: '/login',
    name: 'login.index',
    component: require('pages/login/index/index.vue'),

    // If the user needs to be a guest to view this page
    meta: {
      guest: true,
    },
  },

  // Register
  {
    path: '/register',
    name: 'register.index',
    component: require('pages/register/index/index.vue'),

    // If the user needs to be a guest to view this page
    meta: {
      guest: true,
    },
  },
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/*',
    redirect: '/home',
  },
];



